"use client";

import { useEffect, useState } from "react";
import  supabase  from "@/utils/supabase/client";
import Link from "next/link";

interface Stats {
  categories: number;
  subcategories: number;
  companies: number;
  submissions: number;
}

export default function DashboardPage() {
  const [stats, setStats] = useState<Stats>({
    categories: 0,
    subcategories: 0,
    companies: 0,
    submissions: 0,
  });
  const [loading, setLoading] = useState(true);

  useEffect(() => {
    fetchStats();
  }, []);

  const fetchStats = async () => {
    setLoading(true);
    try {
      const { data: categories, error: catErr } = await supabase
        .from("insurance_categories")
        .select("*", { count: "exact" });
      const { data: subcategories, error: subErr } = await supabase
        .from("insurance_subcategories")
        .select("*", { count: "exact" });
      const { data: companies, error: compErr } = await supabase
        .from("insurance_companies")
        .select("*", { count: "exact" });
      const { data: submissions, error: submErr } = await supabase
        .from("form_submissions")
        .select("*", { count: "exact" });

      if (catErr || subErr || compErr || submErr) {
        console.error(catErr || subErr || compErr || submErr);
        setLoading(false);
        return;
      }

      setStats({
        categories: categories?.length || 0,
        subcategories: subcategories?.length || 0,
        companies: companies?.length || 0,
        submissions: submissions?.length || 0,
      });
    } catch (err) {
      console.error("Failed to fetch stats:", err);
    } finally {
      setLoading(false);
    }
  };

  if (loading) return <div className="p-10 text-center">Loading dashboard...</div>;

  return (
    <main className="p-8">
      <h1 className="text-3xl font-bold mb-6">Admin Dashboard</h1>
      <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-4 gap-6">
        <div className="p-6 bg-white shadow rounded-lg text-center">
          <h2 className="text-xl font-semibold">Categories</h2>
          <p className="text-3xl mt-2">{stats.categories}</p>
          <Link href="/admin/categories" className="text-indigo-600 mt-2 inline-block">
            Manage Categories
          </Link>
        </div>
        <div className="p-6 bg-white shadow rounded-lg text-center">
          <h2 className="text-xl font-semibold">Subcategories</h2>
          <p className="text-3xl mt-2">{stats.subcategories}</p>
          <Link href="/admin/subcategories" className="text-indigo-600 mt-2 inline-block">
            Manage Subcategories
          </Link>
        </div>
        <div className="p-6 bg-white shadow rounded-lg text-center">
          <h2 className="text-xl font-semibold">Companies</h2>
          <p className="text-3xl mt-2">{stats.companies}</p>
          <Link href="/admin/companies" className="text-indigo-600 mt-2 inline-block">
            Manage Companies
          </Link>
        </div>
        <div className="p-6 bg-white shadow rounded-lg text-center">
          <h2 className="text-xl font-semibold">Submissions</h2>
          <p className="text-3xl mt-2">{stats.submissions}</p>
          <Link href="/admin/submissions" className="text-indigo-600 mt-2 inline-block">
            View Submissions
          </Link>
        </div>
      </div>
    </main>
  );
}
